export class InvoiceComponent implements OnInit {
    public invoiceForm: FormGroup;
    formValidate: boolean
    resultQrcode: any;
    invoiceId;
    productData: any;
    channelId: any;
    payId: any;
    receivedBy: any = '';
    comments: any;
    paymentModeList: any = [];
    channelList: any;
    noofProducts;

    constructor(
        private readonly router: Router,
        private readonly _qrScanner: QRCodeScanner,
        private readonly fb: FormBuilder,
        private readonly appservice: AppService,
        public dialog: MatDialog,
        private _sanitizer: DomSanitizer,
        private cdr: ChangeDetectorRef

    ) {
        this.invoiceForm = this.fb.group({
            invoice: [null, [Validators.required]],
            channelId: [null, [Validators.required]],
            payId: [null, [Validators.required]],
            noofProducts: [null, [Validators.required]],
            // image: [null],
            receivedBy: [null, [Validators.required]],
            comments: [null, [Validators.required]],
        })
        // this.invoiceId = this.invoiceForm.controls['INVOICE']
    }


    ngOnInit() {
        /**
         * Back Button event trigger
         */
        history.pushState(null, null, location.href);
        window.onpopstate = function (event) {
            history.go(1);
        };
        this.payId = '';
        this.channelId = ''
        this.appservice.getAllPaymentModes().subscribe((data: any) => {
            if (data.error == null) {
                this.paymentModeList = data.data;
            }
            else {
                this.openDialog(data.error.message)
            }
        });
        this.appservice.getAllChannels().subscribe((data: any) => {
            if (data.error == null) {
                this.channelList = data.data;
            }
            else {
                this.openDialog(data.error.message)
            }
        });
        const receivedBy = this.invoiceForm.get('receivedBy');
        this.invoiceForm.get('payId').valueChanges.subscribe(
            (value) => {
                if (value == 1) {
                    receivedBy.setValidators(Validators.compose([Validators.required]));
                }
                else {
                    receivedBy.clearValidators()
                    receivedBy.updateValueAndValidity()
                }
            });

    }

    change() {
        const comments = this.invoiceForm.get('comments');
        if ((this.invoiceForm.get('channelId').value == 1 && this.invoiceForm.get('payId').value != 8) || (this.invoiceForm.get('channelId').value == 7 && this.invoiceForm.get('payId').value != 8)) {
            comments.setValidators(Validators.compose([Validators.required]));
        }
        else {
            comments.clearValidators()
            comments.updateValueAndValidity()
        }
    }
    changePayModeOptions() {
        if (this.channelId != 7) {
            this.paymentModeList = []
            this.paymentModeList.push(
                { "id": 8, "types": "COD" },
                { "id": 10, "types": "ONLINE" }
            )
        } else {
            this.paymentModeList = []
            this.paymentModeList.push(
                { "id": 1, "types": "CASH" },
                { "id": 2, "types": "CREDIT_CARD" },
                { "id": 3, "types": "NET_BANKING" },
                { "id": 4, "types": "DEBIT_CARD" },
                { "id": 5, "types": "WALLET" },
                { "id": 6, "types": "CHEQUE" },
                { "id": 7, "types": "INDIA_MART" },
                { "id": 8, "types": "COD" },
                { "id": 9, "types": "UPI" },
                { "id": 10, "types": "ONLINE" }
            )
        }
    }

    getInvoiceBybarcode() {
        this._qrScanner.promiseScan().then(result => {
            this.resultQrcode = result;
            if (!this.resultQrcode.cancelled) {
                this.invoiceId = this.resultQrcode.text
                localStorage.setItem('invoiceId', this.resultQrcode.text)
            }
        });
    }
    loaderBtn: boolean = false;
    receiptImg: any = ''; img: any; selectedImage: any;
    images: any = []
    takePicture(field) {
        //this.receiptImg = ""
        //this.img = this._sanitizer.bypassSecurityTrustResourceUrl('data:image/jpg;base64,'
        //    + this.receiptImg);
        // this.receiptImg = this.img.changingThisBreaksApplicationSecurity;
        //this.postObj.proofs = this.receiptImg;
        this.loaderBtn = true;
        this.selectedImage = field;
        if (navigator != undefined) {
            if (this.selectedImage == 2) {
                var options = {
                    quality: 10,
                    allowEdit: true,
                    destinationType: Camera.DestinationType.DATA_URL,
                    sourceType: Camera.PictureSourceType.CAMERA,
                    encodingType: Camera.EncodingType.PNG,
                    targetWidth: 500,
                    targeHeight: 500,
                    correctOrientation: true,
                };
            } else if (this.selectedImage == 1) {
                var options = {
                    quality: 10,
                    allowEdit: true,
                    destinationType: Camera.DestinationType.DATA_URL,
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    encodingType: Camera.EncodingType.PNG,
                    targetWidth: 500,
                    targeHeight: 500,
                    correctOrientation: true
                };
            }
            navigator.camera.getPicture(
                (base64string) => {
                    this.loaderBtn = false;
                    this.receiptImg = base64string;
                    this.cdr.detectChanges();
                    // this.receiptImg = this._sanitizer.bypassSecurityTrustResourceUrl('data:image/jpg;base64,'
                    //     + base64string);            
                },
                (error) => {
                    this.receiptImg = '';
                    alert("Unable to obtain picture: " + error);
                    this.loaderBtn = false;
                    this.cdr.detectChanges();
                }, options);
        }
    }

    deleteReceiptImg() {
        this.receiptImg = '';
        this.cdr.detectChanges();
    }
    submit() {
        if ((this.channelId == 1 && this.payId != 8) || (this.channelId == 7 && this.payId != 8)) {
            if (this.receiptImg != '' && this.invoiceForm.valid) {
                this.navigatetoInvoice()
            } else {
                this.formValidate = true
            }
        } else {
            this.navigatetoInvoice()
        }
    }
    imageId: any = ''; payImage = "Payment Document.png"
    navigatetoInvoice() {
        if (this.invoiceForm.valid) {
            localStorage.setItem('invoiceId', this.invoiceId);
            localStorage.setItem('noofProducts', this.noofProducts);
            localStorage.setItem('paymentMode', this.payId);
            localStorage.setItem('channel', this.channelId);
            localStorage.setItem('receivedBy', this.receivedBy);
            const postObj: any = {
                comments: this.comments,
                invoiceNumber: this.invoiceId,
                proofs: [this.receiptImg]
            };
            // postObj.proofs[0]=this.receiptImg;
            // postObj.comments = this.comments;
            // postObj.invoiceNumber = this.invoiceId;
            if ((this.channelId == 1 && this.payId != 8) || (this.channelId == 7 && this.payId != 8)) {
                this.appservice.getImageId(postObj).subscribe((data: any) => {
                    if (data.error == null) {
                        this.imageId = data.data.id;
                        localStorage.setItem('imageId', this.imageId);
                    }
                    else {

                        this.openDialog(data.error.message)
                    }
                })
            }
            if (this.imageId == '') {
                localStorage.setItem('imageId', this.imageId);
            }
            this.appservice.getDetailsByDispatchInvoice(this.invoiceId).subscribe((data: any) => {
                if (data.error == null) {
                    localStorage.setItem('dispatchType', '1');
                    // localStorage.setItem('QRCode', '0124-0000027')
                    // this.router.navigate(['/dispatch/invoice-view'])
                    this._qrScanner.promiseScan().then(result => {
                        this.resultQrcode = result;
                        if (!this.resultQrcode.cancelled) {
                            localStorage.setItem('QRCode', this.resultQrcode.text)
                            this.router.navigate(['/dispatch/invoice-view'])
                        }
                    })
                }
                else {
                    this.openDialog(data.error.message)
                }

            })

        }
        else {
            this.formValidate = true
        }

    }




    navigatetoHomePage() {
        this.router.navigate(['./dispatch']);
    }
    openDialog(msgText) {
        const dialogRef = this.dialog.open(DialogComponent, { disableClose: true });
        localStorage.setItem('msg', msgText)
        dialogRef.afterClosed().subscribe(result => {
            if (result) {

            }
        });
    }
}

